<template>
    <div id="wrapper">
        <navbar id="nav" class="animate__animated animate__fadeInLeft no-print"  style="display: none;"/>
        <div class="d-flex flex-column animate__animated animate__fadeInRight" id="content-wrapper" style="display: none !important;">
            <div id="content">
                <header-btn/>
                <div class="container-fluid">
                    <h3 class="text-dark text-right">  </h3>
                    <hr>
                    <div class="row" style="overflow-x: auto;">
                        <div class="mb-2 p-1"> 
                            <input type="text" class="form-control text-center" placeholder="Search..." v-model="search" v-on:keyup.enter="search_employee">
                        </div>
                        <button class="btn btn-success mb-2 mx-1" v-on:click="search_employee()"> <i class="fa fa-search"></i> </button>
                        <button class="btn btn-primary mb-2 mx-1" v-on:click="refresh()"> <i class="fa fa-redo"></i> </button>
                        <button class="btn btn-success mb-2 mx-1" v-on:click="get_all()"> All Employees </button>
                        <button class="btn btn-success mb-2 mx-1" v-on:click="get_actived()"> Actived </button>
                        <button class="btn btn-danger mb-2 mx-1" v-on:click="get_deactived()"> Deactived </button>
                        <button class="btn btn-primary mb-2 mx-1" onclick="window.print()"> Print <i class="fa fa-print"></i> </button>
                        <table class="table table-bordered table-striped text-dark text-center" dir="rtl">
                            <thead>
                                <th></th>
                                <th> </th>
                                <th> </th>
                                <th></th>
                                <th style="width: 10%;">.</th>
                                <th style="width: 14%;"> </th>
                                <th style="width: 10%;"> </th>
                                <th style="width: 13%;"> </th>
                                <th style="width: 8%;"> </th>
                                <th class="no-print" style="width: 6%;"></th>
                            </thead>
                            <tbody>
                                <tr v-for="employee in employees" :key="employee.emp_id">
                                    <td> {{ employee.emp_id }} </td>
                                    <td> {{ employee.first_name }} </td>
                                    <td> {{ employee.last_name }} </td>
                                    <td> {{ employee.staff_name }} </td>
                                    <td> {{ employee.phone }} </td>
                                    <td> {{ employee.reg_date.slice(0, 10) }} </td>
                                    <td> {{ employee.salary_type | salary_type_filter }} </td>
                                    <td dir="ltr"> 
                                        <span v-if="employee.salary_type == 'Monthly'"> {{ employee.monthly_salary | money_filter}}  </span>
                                        <span v-else> {{ employee.daily_salary | money_filter}}  </span>
                                        &nbsp; 
                                        <span v-if="employee.salary_type=='Monthly'"> $ </span>
                                        <span v-else> IQD </span>
                                    </td>
                                    <td v-bind:class="{ 'text-success': employee.active_status == 1, 'text-danger': employee.active_status == 0 }"> 
                                        {{ employee.active_status | status_filter }} 
                                    </td>
                                    <td class="no-print">
                                        <button class="btn btn-primary" v-on:click="get_employee(employee.emp_id)" data-toggle="modal" data-target="#mdl_employee"> <i class="fa fa-eye"></i> </button> 
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <div class="float-right mb-3" id="paginations">
                            <button class="btn btn-primary" v-on:click="previos_pagination_btn()">&lt;</button>

                            <button class="btn btn-outline-primary mx-1 pagination-btns"
                                    style="display: none" 
                                    v-for="num in Math.ceil(noOfEmployees / 15)" :key="num"
                                    v-bind:class=" 'pg-group-' + Math.ceil(num / 5) "
                                    v-on:click="get_limited_data(num)"> {{ num }} </button>

                            <button class="btn btn-primary" v-on:click="next_pagination_btn()">&gt;</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Modals -->
        <div class="modal text-dark animate__animated animate__fadeInDown animate__faster" id="mdl_employee">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">
                    <div class="modal-header">
                        <button class="btn btn-danger" data-dismiss="modal"> &times; </button>
                        <h5 class="mt-2">   </h5>
                    </div>
                    <div class="modal-body " dir="rtl">
                        <div class="container-fluid">
                            <div class="text-center"> 
                                <img v-bind:src="single_employee.personal_image_path" alt="No image" class="col-sm-5 mb-2">
                                <img v-bind:src="single_employee.identification_image_path" alt="No image" class="col-sm-5 mb-2">
                            </div>
                            <hr>
                            <div class="row">
                                <label class="col-sm-3 text-center mt-2">  : </label>
                                <label class="col-sm-9 form-control text-center"> {{ single_employee.emp_id }} </label>

                                <label class="col-sm-3 text-center mt-2">   : </label>
                                <label class="col-sm-9 form-control text-center"> {{ single_employee.first_name }} </label>

                                <label class="col-sm-3 text-center mt-2">   : </label>
                                <label class="col-sm-9 form-control text-center"> {{ single_employee.last_name }} </label>

                                <label class="col-sm-3 text-center mt-2">  : </label>
                                <label class="col-sm-9 form-control text-center"> {{ single_employee.staff_name }} </label>

                                <label class="col-sm-3 text-center mt-2">   : </label>
                                <label class="col-sm-9 form-control text-center"> {{ single_employee.phone }} </label>

                                <label class="col-sm-3 text-center mt-2">   : </label>
                                <label class="col-sm-9 form-control text-center"> {{ single_employee.reg_date.slice(0, 10) }} </label>
                                
                                <label class="col-sm-3 text-center mt-2">   : </label>
                                <label class="col-sm-9 form-control text-center"> {{ single_employee.salary_type | salary_type_filter }} </label>

                                <label class="col-sm-3 text-center mt-2">   : </label>
                                <label class="col-sm-9 form-control text-center" dir="ltr" v-if="single_employee.salary_type == 'Monthly'"> {{ single_employee.monthly_salary | money_filter }} $ </label>
                                <label class="col-sm-9 form-control text-center" dir="ltr" v-else> {{ single_employee.daily_salary | money_filter }} IQD </label>

                                <label class="col-sm-3 text-center mt-2">  () : </label>
                                <label class="col-sm-9 form-control text-center" dir="ltr">
                                    {{ single_employee.hour_salary }}  
                                    <span v-if="single_employee.salary_type == 'Monthly'"> $ </span>
                                    <span v-else> IQD </span>
                                </label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import auth from '../../auth.js'
    import showError from "../../showError.js"
    export default {
        name: "employeeReport",
        data() {
            return {
                noOfEmployees: 0,
                employees: [],
                single_employee: {
                    emp_id: null,
                    first_name: null,
                    last_name: null,
                    staff_name: null,
                    phone: null,
                    reg_date: "",
                    salary_type: null,
                    monthly_salary: 0,
                    daily_salary: 0,
                    hour_salary: 0,
                    active_status: null,
                    personal_image_path: null,
                    identification_image_path: null
                },
                search: "",
                current_pagination: 1,
                max_pagination: 0
            }
        },
        beforeCreate() {
            auth("employeeReport");
        },
        created() {
            this.$http.post("/employee/getNoOfEmployees").then(({data}) => {
                this.noOfEmployees = data.noOfEmployees;
            }).catch((err)=>{
                showError(err);
            });

            this.get_limited_data(1);
        },
        mounted() {
            var load_paginations = setInterval(() => {
                var btns = document.getElementsByClassName("pagination-btns");
                if(btns.length > 0){
                    for(let i=0; i < btns.length; i++){
                        btns[i].style.display = 'inline';
                    }
                    this.max_pagination = Number((btns[btns.length - 1]).classList[4].split("-")[2]);
                    clearInterval(load_paginations);
                }
            }, 750);
        },
        methods: {
            next_pagination_btn() {
                if(this.current_pagination < this.max_pagination){
                    var btns_to_hide = document.getElementsByClassName("pg-group-" + this.current_pagination);
                    for(let i=0; i<btns_to_hide; i++){
                        btns_to_hide[i].style.display = 'none';
                    }
                    ++this.current_pagination;
                    var btns_to_show = document.getElementsByClassName("pg-group-" + this.current_pagination);
                    for(let i=0; i<btns_to_show; i++){
                        btns_to_show[i].style.display = 'inline';
                    }
                }
            },
            previos_pagination_btn() {
                if(this.current_pagination > 1){
                    var btns_to_hide = document.getElementsByClassName("pg-group-" + this.current_pagination);
                    for(let i=0; i<btns_to_hide; i++){
                        btns_to_hide[i].style.display = 'none';
                    }
                    --this.current_pagination;
                    var btns_to_show = document.getElementsByClassName("pg-group-" + this.current_pagination);
                    for(let i=0; i<btns_to_show; i++){
                        btns_to_show[i].style.display = 'inline';
                    }
                }
            },
            get_limited_data(paginate_number) {
                var offset = 0;
                if(paginate_number > 1){
                    offset = (paginate_number - 1) * 15;
                }
                this.$http.post('/employee/getData', {
                    offset
                }).then(({data})=>{
                    this.employees = data;
                }).catch((err)=>{
                    showError(err);
                });
            },
            get_employee(emp_id) {
                const [employee] = this.employees.filter(obj  => obj.emp_id == emp_id);
                this.single_employee.emp_id = employee.emp_id;
                this.single_employee.first_name = employee.first_name;
                this.single_employee.last_name = employee.last_name;
                this.single_employee.staff_name = employee.staff_name;
                this.single_employee.phone = employee.phone;
                this.single_employee.reg_date = employee.reg_date;
                this.single_employee.salary_type = employee.salary_type;
                this.single_employee.monthly_salary = employee.monthly_salary;
                this.single_employee.daily_salary = employee.daily_salary;
                this.single_employee.hour_salary = employee.hour_salary;
                this.single_employee.active_status = employee.active_status;
                this.single_employee.personal_image_path = employee.personal_image_path;
                this.single_employee.identification_image_path = employee.identification_image_path;
            },
            get_all() {
                this.$http.post("/employee/getAll").then(({data}) => {
                    this.employees = data;
                    document.getElementById('paginations').style.display = 'none';
                });
            },
            get_actived() {
                this.$http.post("/employee/getActived").then(({data}) => {
                    this.employees = data;
                    document.getElementById('paginations').style.display = 'none';
                });
            },
            get_deactived() {
                this.$http.post("/employee/getDeactived").then(({data}) => {
                    this.employees = data;
                    document.getElementById('paginations').style.display = 'none';
                });
            },
            search_employee() {
                if(this.search == ""){
                    this.$alert("Write a thing to search ...", "Empty Field", "warning");
                } else {
                    this.$http.post("/employee/searchEmployee", {
                        search_value: this.search
                    }).then(({data}) => {
                        this.employees = data;
                        document.getElementById('paginations').style.display = 'none';
                    });
                }
            },
            refresh(){
                this.get_limited_data(1);
                this.search = "";
                document.getElementById('paginations').style.display = 'block';
            }
        },
        filters: {
            status_filter(value) {
                return ["Deactive", "Active"][value];
            },
            money_filter(value) {
                var num = value.toString().split("").reverse();
                var arr = [];
                for (let i = 0; i < num.length; i++) {
                    arr.unshift(num[i]);
                    if((i + 1) % 3 == 0){
                        arr.unshift(",");
                    }
                }
                var num_with_comma = arr.join("");
                if(num_with_comma.charAt(0) == ','){
                    return num_with_comma.slice(1);
                }
                return num_with_comma;
            },
            salary_type_filter(value) {
                if(value == "Monthly"){
                    return "";
                }
                return "";
            }
        }
    }
</script>

<style scoped>
    @font-face {
        font-family: nrt;
        src: url("../../assets/fonts/NRT-Reg.ttf");
    }
    table, h3, .modal {
        font-family: nrt;
    }
    @media print {
        input, button, .no-print {
            display: none !important;
        }
    }
</style>