<template>
    <div id="wrapper">
        <navbar id="nav" class="no-print animate__animated animate__fadeInLeft"  style="display: none;"/>
        <div class="d-flex flex-column animate__animated animate__fadeInRight" id="content-wrapper" style="display: none !important;">
            <div id="content">
                <header-btn/>
                <div class="container-fluid text-dark">
                    <div class="row px-1 py-3 mb-1 no-print">
                        <label class="mt-1 col-xl-1 mb-2"> Month: </label>
                        <select class="form-control col-xl-1 col-md-4 mb-2" v-model="month">
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                        </select>
                        <label class="mt-1 col-xl-1 mb-2"> Year: </label>
                        <input type="number" min="2020" class="form-control col-xl-1 col-md-4 mb-2" v-model="year">
                        <label class="mt-1 col-xl-2 mb-2"> Phone Number: </label>
                        <input type="text" maxlength="11" class="form-control col-xl-2 col-md-4 mb-2" v-model="phone">
                        <button class="btn btn-success col-xl-2 col-md-4 mx-2 mb-2" v-on:click="search()"> Search <i class="fa fa-search"></i> </button><hr>
                    </div>
                    <hr>
                    <button class="no-print btn btn-info btn-block mb-3" data-toggle="collapse" data-target="#staffs"> Staffs <i class="fa fa-arrow-down"></i> </button>
                    <div class="collapse" id="staffs">
                        <div class="row no-print">
                            <label class="col-sm-1 mt-1"> Staffs: </label>
                            <select v-model="staff_id" class="form-control col-sm-3 mb-2">
                                <option value=""></option>
                                <option v-for="staff in staffs" :key="staff.st_id" v-bind:value="staff.st_id"> {{ staff.staff_name }} </option>
                            </select>
                        </div>
                        <div class="row no-print">
                            <div class="col-sm-2 mb-2" v-for="emp in employees" :key="emp.phone">
                                <button class="btn btn-primary btn-block" v-on:click="phone = emp.phone"> {{ emp.full_name }} </button>
                            </div>
                        </div>
                    </div>
                    
                    <hr class="no-print">
                    <div class="no-print row">
                        <label class="mt-1 mx-2"> 1 $ =  </label>
                        <input type="number" min="0" class="form-control col-xl-1 text-center" v-model="dollar_price" v-bind:readonly="gs_id != null">
                        <label class="mt-1 mx-2"> IQD </label>
                    </div>
                    <hr class="no-print">
                    <div class="row px-1 py-1 mb-2" id="report" dir="rtl">
                        <div class="col-12 text-right py-1" style="border: 1px solid; overflow-x: auto;" >
                            <img v-bind:src="employee.personal_image" width="12%" style="border: 1px solid;" class="mb-3 float-left">

                            <button class="btn btn-success float-left ml-1 no-print" v-if="gs_id != null" data-toggle="modal" data-target="#mdl_give_detail">   &nbsp; <i class="fa fa-check-circle"></i> </button>
                            <button class="btn btn-primary float-left ml-1 no-print" v-else v-on:click="create_monthly_receipt()">    &nbsp; <i class="fa fa-plus"></i> </button>
                            
                            <span> :  {{ month + '/' + year }} </span>  
                            <br><br>
                            <span style="width: 20%; display: inline-block;"> :  {{ employee.emp_id }} </span> 
                            <span style="width: 20%; display: inline-block;">  :  {{ phone }} </span> 
                            <br><br> 
                            <span> :  {{ employee.full_name }} </span>
                            <br><br>
                            <span> :  {{ employee.staff_name }} </span>  
                            <br><br>
                            <span style="width: 21%; display: inline-block;">  :  {{ employee.salary_type | salary_type_filter }} </span>
                            <span style="width: 25%; display: inline-block;" v-if="employee.salary_type == 'Monthly'">   : 
                                <span> {{ employee.monthly_salary }}  </span>
                                <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                <span v-else> IQD </span>
                            </span> 
                            <span style="width: 25%; display: inline-block;">   : 
                                <span> {{ employee.daily_salary | money_filter }}  </span>
                                <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                <span v-else> IQD </span>
                            </span> 
                            <span style="width: 29%; display: inline-block;"> / : 
                                <span> {{ employee.hour_salary | money_filter }}  </span>
                                <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                <span v-else> IQD </span>
                            </span> 
                            <hr>
                            <span style="width:33%; display: inline-block;">   () : 
                                <span> {{ employee.total_worked_hour }}  </span>
                            </span>
                            <span style="width:33%; display: inline-block;">    () : 
                                <span> {{ employee.total_overtime }}  </span>
                            </span>
                            <span style="width:33%; display: inline-block;">   () : 
                                <span> {{ employee.total_h_not_work }}  </span>
                            </span>
                            <br><br>
                            <span style="width:33%; display: inline-block;">   : 
                                <span> {{ employee.total_present }}  </span>
                            </span>
                            <span style="width:33%; display: inline-block;">   () : 
                                <span> {{ employee.total_off }}  </span>
                            </span>
                            <span style="width:33%; display: inline-block;">   : 
                                <span> {{ employee.total_absent }}  </span>
                            </span>
                            
                            <hr>
                            <div style="width: 70%; display: inline-block;">
                                <span style="width:100%; display: inline-block;">  1  : 
                                    <span> {{ dollar_price }}  </span>
                                    <span> IQD </span>
                                </span>
                                <span style="width:100%; display: inline-block;">    : 
                                    <span> {{ employee.total_fine | money_filter}}  </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ <i class="fa fa-minus text-danger"></i> </span>
                                    <span v-else> IQD <i class="fa fa-minus text-danger"></i> </span>
                                </span>
                                <span style="width:100%; display: inline-block;">     : 
                                    <span> {{ employee.total_not_work_s | money_filter}}  </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ <i class="fa fa-minus text-danger"></i> </span>
                                    <span v-else> IQD <i class="fa fa-minus text-danger"></i> </span>
                                </span>
                                <span style="width:100%; display: inline-block;">     : 
                                    <span> {{ employee.total_apsen_s | money_filter}}  </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ <i class="fa fa-minus text-danger"></i> </span>
                                    <span v-else> IQD <i class="fa fa-minus text-danger"></i> </span>
                                </span>
                                <span style="width:100%; display: inline-block;">     : 
                                    <span> {{ employee.total_o_s | money_filter}}  </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ <i class="fa fa-plus text-success"></i> </span>
                                    <span v-else> IQD <i class="fa fa-plus text-success"></i> </span>
                                </span>
                                <span style="width:100%; display: inline-block;">       : 
                                    <span> {{ employee.total_final_s | money_filter}}  </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                    <span v-else> IQD </span>
                                </span>
                                <span style="width:100%; display: inline-block;">        : 
                                    <span> {{ employee.total_s_by_h_work | money_filter}} </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                    <span v-else> IQD </span>
                                </span>
                                <span style="width:100%; display: inline-block;">      : 
                                    <span> {{ employee.gived_salary | money_filter}} </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                    <span v-else> IQD </span>
                                </span>
                                <span style="width:100%; display: inline-block;">       : 
                                    <span v-if="(Number(employee.total_final_s) - Number(employee.gived_salary)).toFixed(3) > 0"> {{ (Number(employee.total_final_s) - Number(employee.gived_salary)).toFixed(3) || 0 | money_filter }} </span>
                                    <span v-else> 0 </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                    <span v-else> IQD </span>
                                </span>
                                <span style="width:100%; display: inline-block;">      : 
                                    <span v-if="(Number(employee.total_s_by_h_work) - Number(employee.gived_salary)).toFixed(3) > 0"> {{ (Number(employee.total_s_by_h_work) - Number(employee.gived_salary)).toFixed(3) || 0 | money_filter }} </span>
                                    <span v-else> 0 </span>
                                    <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                    <span v-else> IQD </span>
                                </span>
                            </div>
                            <div style="width: 30%; display: inline;" class="float-left">  
                                <table class="table table-bordered text-dark text-center">
                                    <thead>
                                        <th> </th>
                                        <th> </th>
                                    </thead>
                                    <tbody>
                                        <tr v-for="salary in each_give_salary" :key="salary.gsd_id">
                                            <td> 
                                                {{ salary.gived_salary }} 
                                                <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                                <span v-else> IQD </span>
                                            </td>
                                            <td> {{ salary.gived_date | date_filter }} </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <hr>
                            <table class="table table-bordered text-center text-dark">
                                <thead>
                                    <th style="width: 10%;"> . </th>
                                    <th>  </th>
                                    <th style="width: 16%;">   </th>
                                    <th style="width: 14%;">  </th>
                                    <th style="width: 14%;">  () </th>
                                    <th style="width: 13%;">  () </th>
                                    <th style="width: 13%;">   </th>
                                    <th style="width: 13%;">   </th>
                                    <th style="width: 10%;">  </th>
                                </thead>
                                <tbody>
                                    <tr v-for="day in each_days" :key="day.dsl_id" v-bind:class="{ 'bg-danger text-white': day.absent == 1 }">
                                        <td> {{ day.dsl_id }} </td>
                                        <td> {{ day.staff_name }} </td>
                                        <td> {{ day.work_date.slice(0,10) }} </td>
                                        <td> {{ day.location }} </td>
                                        <td> {{ day.worked_hours }} </td>
                                        <td> {{ day.overtime }} </td>
                                        <td> {{ day.fine_reason }} </td>
                                        <td> {{ day.fine }} </td>
                                        <td> {{ day.absent | absent_filter}} </td>
                                    </tr>
                                </tbody>
                            </table>
                            <div class="alert alert-primary text-center">  : {{ employee.full_name }} </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Modals -->
        <div class="modal animate__animated animate__fadeInDown animate__faster" id="mdl_give_detail">
            <div class="modal-dialog text-dark">
                <div class="modal-content">
                    <div class="modal-header">
                        <button class="btn btn-danger" data-dismiss="modal"> &times; </button>
                        <h5 class="mt-2">   </h5>
                    </div>
                    <div class="modal-body">
                        <div v-if="employee.gived_salary >= employee.monthly_salary" class="alert alert-primary text-center">
                                  
                        </div>
                        <div v-else class="container-fluid" dir="rtl">
                            <div class="row mb-2">
                                <label class="col-sm-3 mt-2 text-center"> :  </label>
                                <label class="form-control col-sm-8 text-center"> {{ new Date().toISOString().split("T")[0] }} </label>
                            </div>
                            <div class="row mb-2">
                                <label class="col-sm-3 mt-2 text-center"> :  </label>
                                <input type="number" class="form-control col-sm-8 text-center" v-model.lazy="salary">
                                <label v-if="employee.salary_type == 'Monthly'" class="col-sm-1 mt-2"> $ </label>
                                <label v-else class="col-sm-1 mt-2"> IQD </label>
                            </div>
                            <div class="alert alert-warning text-center">
                                    : {{ ((Number(employee.total_final_s) - Number(employee.gived_salary)).toFixed(3) - Number(salary)).toFixed(3)  }} 
                                <label v-if="employee.salary_type == 'Monthly'" class="col-sm-1 mt-2"> $ </label>
                                <label v-else class="col-sm-1 mt-2"> IQD </label>
                            </div>
                            <div class="alert alert-primary text-right">
                                -    :
                                <br>
                                 1     
                                <br>
                                 1000     
                            </div>
                            <button class="btn btn-primary btn-block" id="btn_give_salary" v-on:click="give_salary()">   &nbsp; <i class="fa fa-dollar-sign fa-sm"></i> </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import auth from '../auth';
import showError from '../showError';
    export default {
        name: "employeeProfile",
        data() {
            return {
                month: new Date().getMonth() + 1,
                year: new Date().getFullYear(),
                phone: "",
                gs_id: null,
                salary: 0,
                employee: {
                    emp_id: null,
                    full_name: null,
                    staff_name: null,
                    salary_type: null,
                    monthly_salary: 0,
                    daily_salary: 0,
                    hour_salary: 0,
                    total_worked_hour: 0,
                    total_not_work_s: 0,
                    total_overtime: 0,
                    total_o_s: 0,
                    total_apsen_s: 0,
                    total_absent: 0,
                    total_present: 0,
                    total_fine: 0,
                    total_s_by_h_work: 0,
                    total_final_s:0,
                    gived_salary: 0,
                    personal_image: null
                },
                each_days: [],
                each_give_salary: [],
                dollar_price: 0,
                employees_btn:{
                    full_name:null
                },
                staff_id:"",
                staffs: [],
                employees: []
            }
        },
        beforeCreate(){
            auth("give_salary");
        },
        created(){
            this.$http.post("/staff/getData").then(({data}) => {
                this.staffs = data;
            });
        },
        methods: {
            search() {
                this.$http.post('/employee/getEmployeeInfo/'+this.phone+'/'+this.month+'/'+this.year).then(({data})=>{
                    this.gs_id = data.gs_id;
                    this.employee.emp_id = data.employee.emp_id;
                    this.employee.full_name = data.employee.full_name;
                    this.employee.staff_name = data.employee.staff_name;
                    this.employee.salary_type = data.employee.salary_type;
                    this.employee.personal_image = data.employee.personal_image_path;
                    this.employee.total_worked_hour = data.employee.total_h;
                    this.employee.total_overtime = data.employee.total_o;
                    this.employee.total_fine = data.employee.total_fine;
                    this.employee.total_present = data.employee.count_present;
                    this.employee.total_absent = data.employee.total_apsent
                    this.employee.hour_salary = data.employee.hour_salary;
                    this.employee.daily_salary = data.employee.daily_salary;
                    this.employee.monthly_salary = data.employee.monthly_salary;
                    this.employee.total_off = data.employee.count_off;
                    this.employee.total_h_not_work = data.employee.total_h_not_work;
                    this.employee.total_o_s = data.employee.total_o_s;
                    this.employee.total_not_work_s = data.employee.total_not_work_s;
                    this.employee.total_apsen_s = data.employee.total_apsen_s;
                    this.employee.total_off_s = data.employee.total_off_s;
                    this.employee.total_final_s = data.employee.total_final_s - data.employee.total_fine;
                    this.employee.gived_salary = data.employee.gived_salary;
                    this.employee.total_s_by_h_work = (data.employee.total_s_by_h_work + data.employee.total_o_s + data.employee.total_off_s - data.employee.total_fine).toFixed(3);
                    this.dollar_price = data.employee.dollar_price || this.dollar_price;

                    this.each_days = data.each_days;
                    
                    this.each_give_salary = data.each_give_salary;

                   
                });
            },
            create_monthly_receipt() {
                if(this.phone.length == 11){
                    if(this.dollar_price <= 0 && this.employee.salary_type == 'Monthly'){
                        this.$alert("Enter Dollar price", "", "warning");
                    } else {
                        this.$confirm(`      ${this.month}/${this.year}`, "", "question").then(() => {
                            this.$http.post("/gived_salary/addGiveSalary", {
                                emp_id: this.employee.emp_id,
                                salary_month: this.month,
                                salary_year: this.year,
                                monthly_salary: this.employee.monthly_salary,
                                daily_salary: this.employee.daily_salary,
                                hour_salary: this.employee.hour_salary,
                                dollar_price: this.dollar_price
                            }).then(({data}) => {
                                this.gs_id = data.gs_id;
                                this.$alert("", "Success", "success");
                                this.$http.post('/employee/getEmployeeInfo/'+this.phone+'/'+this.month+'/'+this.year).then(({data})=>{
                                 this.employee.total_fine = data.employee.total_fine;
                                 this.employee.total_final_s=data.employee.total_final_s-data.employee.total_fine
                                 this.employee.total_s_by_h_work=(data.employee.total_s_by_h_work + data.employee.total_o_s + data.employee.total_off_s - data.employee.total_fine).toFixed(3)
                                  });
                            }).catch((err) => {
                                showError(err);
                            });
                        });
                    }
                } else {
                    this.$alert("Enter valid phone number", "Invalid phone number", "warning");
                }
            },
            give_salary() {
                if(this.valid_give_salary){
                    if(this.salary > 0){
                        this.$confirm("        ", "", "question").then(() => {
                            document.getElementById("btn_give_salary").style.display = 'none';
                            this.$http.post("/gived_salary/addGivedDetail", {
                                gs_id: this.gs_id,
                                gived_salary: this.salary
                            }).then(() => {
                                this.each_give_salary.push({
                                    gived_salary: this.salary,
                                    gived_date: new Date().toISOString().split("T")[0]
                                });
                                this.employee.gived_salary += Number(this.salary);
                                this.salary = 0;
                                this.$alert("Salary gived", "Success", "success");
                            }).catch((err) => {
                                showError(err);
                            }).finally(() => {
                                var btn = document.getElementById("btn_give_salary");
                                if(btn){
                                    btn.style.display = '';
                                }
                            });
                        });
                    } else {
                        this.$alert("     0", "", "warning");
                    }
                } else {
                    this.$alert("     ", "", "warning");
                }
            },
            search_staff(){
                this.$http.post('/employee/getEmployeeBystaff/'+this.staff_id).then(([data])=>{
                    this.employees_btn.full_name=data.full_name;
                })
            }
        },
        filters: {
            gived_status_filter(value) {
                if(value == 1){
                    return "";
                } 
                return "";
            },
            absent_filter(value) {
                if(value == 0){
                    return "";
                } else if(value == 1){
                    return "";
                } 
                return "";
            },
            salary_type_filter(value) {
                if(value == "Monthly"){
                    return "";
                }
                return "";
            },
            money_filter(value) {
                var num = [];
                var point = "";
                value = value.toString();
                if(value.includes('.')){
                    point = value.substring(value.indexOf('.'));
                    num = value.toString().substring(0, value.indexOf('.')).split("").reverse();
                } else {
                    num = value.split("").reverse();
                }
                var arr = [];
                for (let i = 0; i < num.length; i++) {
                    arr.unshift(num[i]);
                    if((i + 1) % 3 == 0){
                        arr.unshift(",");
                    }
                }
                if(point == ".000"){
                    point = "";
                }
                var num_with_comma = arr.join("") + point;
                if(num_with_comma.charAt(0) == ','){
                    return num_with_comma.slice(1);
                }
                return num_with_comma;
            },
            date_filter(value) {
                var d = new Date(value);
                d.setDate(d.getDate() + 1);
                return d.toISOString().split("T")[0];
            }
        },
        watch: {
            phone(value) {
                if(value.length == 11){
                    this.search();
                }
            },
            month() {
                if(this.phone.length == 11){
                    this.search();
                }
            }, 
            year(){
                if(this.phone.length == 11){
                    this.search();
                }
            },
            staff_id(value){
                if(value != null){
                    this.$http.post("/employee/getEmployeeBystaff/" + this.staff_id).then(({data}) => {
                        this.employees = data;
                    });
                } else {
                    this.employees = [];
                }
            }
        },
        computed: {
            valid_give_salary() {
                if(this.employee.salary_type == 'Daily'){
                    return this.salary <= Number((Number(this.employee.total_final_s) - Number(this.employee.gived_salary)).toFixed(3)) + Number(1000);
                }
                return this.salary <= Number((Number(this.employee.total_final_s) - Number(this.employee.gived_salary)).toFixed(3)) + Number(1);
            }
        }
    }
</script>

<style scoped>
    @font-face {
        font-family: nrt;
        src: url("../assets/fonts/NRT-Reg.ttf");
    }
    #report, .modal {
        font-family: nrt;
    }
    tbody td {
        padding: .5rem;
    }
    @media print {
        .no-print, #nav {
            display: none;
        }
    }
</style>